"use client";

import { useEffect, useMemo, useState } from "react";
import { LineChart, Line, XAxis, YAxis, Tooltip, CartesianGrid, ResponsiveContainer } from "recharts";
import { AlertTriangle, Clock, MapPin, TrendingUp } from "lucide-react";

import { Navbar } from "@/components/ui/Navbar";
import { fetchSegments, fetchDatathonForecast, searchPredictions } from "@/lib/api";
import { ALL_ROAD_SEGMENTS } from "@/lib/roadSegmentData";

const horizonOptions = [60, 120, 360, 1440]; // minutes => 1h, 2h, 6h, 24h

export default function AnalyticsPage() {
    const [segments, setSegments] = useState([]);
    const [liveReadings, setLiveReadings] = useState([]);
    const [selectedSegment, setSelectedSegment] = useState(null);
    const [forecast, setForecast] = useState(null);
    const [loadingForecast, setLoadingForecast] = useState(false);
    const [error, setError] = useState("");
    const [horizonMinutes, setHorizonMinutes] = useState(360);
    const [fromRoad, setFromRoad] = useState("");
    const [toRoad, setToRoad] = useState("");

    // Fetch metadata + live congestion snapshots (synthetic when backend lacks live stream)
    useEffect(() => {
        fetchSegments().then(setSegments).catch(() => {});
        searchPredictions().then(setLiveReadings).catch(() => {});
    }, []);

    // Default selection: top hotspot once readings load
    useEffect(() => {
        if (selectedSegment || !liveReadings.length) return;
        const top = getHotspots(liveReadings)[0];
        if (top) setSelectedSegment(top);
    }, [liveReadings, selectedSegment]);

    // Fetch forecast when selected changes
    useEffect(() => {
        if (!selectedSegment) return;
        setLoadingForecast(true);
        setError("");
        fetchDatathonForecast(
            selectedSegment.road_id,
            new Date().toISOString(),
            selectedSegment.congestion_pct,
        )
            .then((data) => setForecast(data))
            .catch((err) => setError(err.message))
            .finally(() => setLoadingForecast(false));
    }, [selectedSegment]);

    const hotspots = useMemo(() => getHotspots(liveReadings), [liveReadings]);

    const chartData = useMemo(() => {
        if (!forecast) return [];
        const entries = [
            { key: "pred_congestion_plus_1h", hours: 1 },
            { key: "pred_congestion_plus_2h", hours: 2 },
            { key: "pred_congestion_plus_6h", hours: 6 },
            { key: "pred_congestion_plus_24h", hours: 24 },
        ];
        const maxHours = horizonMinutes / 60;
        const now = new Date(forecast.timestamp || Date.now());
        return entries
            .filter((e) => e.hours <= maxHours)
            .map((e) => ({
                label: `+${e.hours}h`,
                time: new Date(now.getTime() + e.hours * 3600 * 1000)
                    .toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" }),
                value: (forecast[e.key] ?? 0) * 100,
            }));
    }, [forecast, horizonMinutes]);

    const roadsByName = useMemo(() => {
        const all = segments.length ? segments : ALL_ROAD_SEGMENTS;
        return Array.from(new Set(all.map((r) => r.road_name))).sort();
    }, [segments]);

    const travelEstimate = useMemo(() => {
        if (!fromRoad || !toRoad || !selectedSegment) return null;
        const distanceKm = 5; // fallback distance
        const freeFlow = 45; // km/h
        const congestion = (selectedSegment.congestion_pct ?? 50) / 100;
        const speed = Math.max(5, freeFlow * (1 - congestion));
        const baseTimeH = distanceKm / freeFlow;
        const currentTimeH = distanceKm / speed;
        return {
            standardMin: Math.round(baseTimeH * 60),
            currentMin: Math.round(currentTimeH * 60),
            delayMin: Math.max(0, Math.round((currentTimeH - baseTimeH) * 60)),
        };
    }, [fromRoad, toRoad, selectedSegment]);

    return (
        <main className="min-h-screen bg-gradient-to-b from-[#05060a] via-[#0b0f1a] to-[#05060a] text-foreground pb-20">
            <Navbar />
            <div className="pt-24 px-4 max-w-7xl mx-auto space-y-6">
                <header className="flex flex-col gap-2">
                    <div className="flex items-center gap-2 text-xs uppercase tracking-[0.2em] text-cyan-400/70">
                        <div className="h-px w-8 bg-cyan-500" />
                        Live Predictive Ops
                    </div>
                    <div className="flex items-center justify-between flex-wrap gap-3">
                        <div>
                            <h1 className="text-3xl font-semibold text-white">Analytics Dashboard</h1>
                            <p className="text-sm text-slate-400">Linked hotspots → forecast → travel estimator.</p>
                        </div>
                        <div className="flex items-center gap-2 text-xs text-slate-400">
                            <Clock className="h-4 w-4 text-cyan-400" /> Live data stream
                        </div>
                    </div>
                </header>

                <div className="grid grid-cols-1 lg:grid-cols-3 gap-5">
                    <section className="lg:col-span-2 rounded-xl border border-cyan-500/10 bg-slate-950/60 shadow-[0_0_0_1px_rgba(0,255,255,0.08)] p-4 flex flex-col gap-4">
                        <div className="flex items-center justify-between gap-3">
                            <div>
                                <h3 className="text-lg font-semibold text-white flex items-center gap-2">
                                    <TrendingUp className="h-5 w-5 text-cyan-400" /> Forecast
                                </h3>
                                <p className="text-xs text-slate-400">Datathon multi-horizon model, segment-linked.</p>
                            </div>
                            <div className="flex items-center gap-2 text-xs text-slate-300">
                                Horizon
                                <select
                                    value={horizonMinutes}
                                    onChange={(e) => setHorizonMinutes(Number(e.target.value))}
                                    className="bg-slate-900 border border-cyan-500/30 text-cyan-100 text-xs rounded-md px-2 py-1"
                                >
                                    {horizonOptions.map((m) => (
                                        <option key={m} value={m}>{m >= 1440 ? "24h" : `${m / 60}h`}</option>
                                    ))}
                                </select>
                            </div>
                        </div>

                        <div className="min-h-[320px] rounded-lg bg-gradient-to-br from-slate-900 via-slate-950 to-black border border-cyan-500/10 p-3">
                            {error && <p className="text-xs text-red-400 mb-2">{error}</p>}
                            {loadingForecast && <p className="text-xs text-slate-300">Fetching forecast…</p>}
                            {!loadingForecast && chartData.length === 0 && (
                                <p className="text-xs text-slate-400">Select a hotspot to load predictions.</p>
                            )}
                            {chartData.length > 0 && (
                                <ResponsiveContainer width="100%" height={320} minWidth={0}>
                                    <LineChart data={chartData} margin={{ top: 20, right: 20, bottom: 10, left: 0 }}>
                                        <defs>
                                            <linearGradient id="glow" x1="0" y1="0" x2="0" y2="1">
                                                <stop offset="5%" stopColor="#22d3ee" stopOpacity={0.8} />
                                                <stop offset="95%" stopColor="#22d3ee" stopOpacity={0.05} />
                                            </linearGradient>
                                        </defs>
                                        <CartesianGrid stroke="#0f172a" strokeDasharray="3 3" />
                                        <XAxis dataKey="label" stroke="#94a3b8" fontSize={12} tickLine={false} axisLine={false} />
                                        <YAxis stroke="#94a3b8" fontSize={12} tickLine={false} axisLine={false} domain={[0, 100]} tickFormatter={(v) => `${v}%`} />
                                        <Tooltip
                                            contentStyle={{ background: "#0b1220", border: "1px solid rgba(34,211,238,0.3)", borderRadius: 8 }}
                                            labelStyle={{ color: "#e2e8f0" }}
                                            formatter={(v) => [`${v.toFixed(1)}%`, "Congestion"]}
                                        />
                                        <Line type="monotone" dataKey="value" stroke="#22d3ee" strokeWidth={3} dot={{ r: 4, fill: "#22d3ee" }} activeDot={{ r: 6, fill: "#22d3ee" }} fill="url(#glow)" />
                                    </LineChart>
                                </ResponsiveContainer>
                            )}
                        </div>
                        {selectedSegment && (
                            <div className="text-xs text-slate-300">
                                Showing {selectedSegment.segment_name} ({selectedSegment.road_id}) · {chartData.length} horizons · live congestion {selectedSegment.congestion_pct?.toFixed?.(1) ?? "--"}%
                            </div>
                        )}
                    </section>

                    <section className="rounded-xl border border-amber-400/20 bg-slate-950/70 p-4 shadow-[0_0_0_1px_rgba(251,191,36,0.18)] flex flex-col gap-3">
                        <div className="flex items-center justify-between">
                            <h3 className="text-lg font-semibold text-white flex items-center gap-2">
                                <AlertTriangle className="h-5 w-5 text-amber-400" /> Top 5 Critical Roads
                            </h3>
                            <span className="text-[10px] px-2 py-1 rounded-full bg-amber-400/10 text-amber-200 border border-amber-400/30">Live</span>
                        </div>
                        <div className="space-y-2">
                            {hotspots.map((seg) => (
                                <button
                                    key={seg.road_id}
                                    onClick={() => setSelectedSegment(seg)}
                                    className={`w-full text-left rounded-lg border px-3 py-2 transition-colors ${
                                        selectedSegment?.road_id === seg.road_id
                                            ? "border-cyan-400/60 bg-cyan-900/30"
                                            : "border-amber-400/20 bg-amber-400/5 hover:border-amber-300/40"
                                    }`}
                                >
                                    <div className="flex items-center justify-between text-sm text-white">
                                        <span>{seg.segment_name}</span>
                                        <span className="text-amber-200 font-semibold">{seg.congestion_pct.toFixed(1)}%</span>
                                    </div>
                                    <div className="flex items-center gap-2 text-[11px] text-amber-200/90">
                                        <MapPin className="h-3 w-3" /> {seg.road_name}
                                    </div>
                                </button>
                            ))}
                            {!hotspots.length && (
                                <p className="text-xs text-amber-200/70">Waiting for live readings…</p>
                            )}
                        </div>
                    </section>
                </div>

                <section className="grid grid-cols-1 md:grid-cols-2 gap-5">
                    <div className="rounded-xl border border-cyan-500/15 bg-slate-950/70 p-4 shadow-[0_0_0_1px_rgba(34,211,238,0.08)]">
                        <div className="flex items-center justify-between mb-3">
                            <h3 className="text-lg font-semibold text-white flex items-center gap-2">
                                <Clock className="h-5 w-5 text-cyan-400" /> Travel Estimator
                            </h3>
                        </div>
                        <div className="grid grid-cols-1 sm:grid-cols-2 gap-3">
                            <SelectInput label="From" value={fromRoad} onChange={setFromRoad} options={roadsByName} />
                            <SelectInput label="To" value={toRoad} onChange={setToRoad} options={roadsByName} />
                        </div>
                        {travelEstimate ? (
                            <div className="mt-4 grid grid-cols-3 gap-2 text-center text-sm text-slate-200">
                                <Metric label="Standard" value={`${travelEstimate.standardMin} min`} subtle="Free-flow" />
                                <Metric label="Current" value={`${travelEstimate.currentMin} min`} subtle="With congestion" />
                                <Metric label="Delay" value={`+${travelEstimate.delayMin} min`} subtle="Extra time" />
                            </div>
                        ) : (
                            <p className="mt-4 text-xs text-slate-400">Pick origin and destination to see the smart estimate.</p>
                        )}
                    </div>
                </section>
            </div>
        </main>
    );
}

function getHotspots(readings) {
    return (readings || [])
        .filter((s) => (s.congestion_pct ?? 0) > 65)
        .sort((a, b) => b.congestion_pct - a.congestion_pct)
        .slice(0, 5);
}

function SelectInput({ label, value, onChange, options }) {
    return (
        <label className="text-xs text-slate-300 space-y-1 block">
            <span className="text-slate-400">{label}</span>
            <select
                value={value}
                onChange={(e) => onChange(e.target.value)}
                className="w-full bg-slate-900 border border-cyan-500/30 text-cyan-50 rounded-md px-3 py-2 text-sm"
            >
                <option value="">Select road</option>
                {options.map((opt) => (
                    <option key={opt} value={opt}>{opt}</option>
                ))}
            </select>
        </label>
    );
}

function Metric({ label, value, subtle }) {
    return (
        <div className="rounded-lg border border-cyan-500/20 bg-slate-900/60 px-3 py-2">
            <div className="text-[11px] uppercase tracking-wide text-cyan-300/80">{label}</div>
            <div className="text-lg font-semibold text-white">{value}</div>
            <div className="text-[11px] text-slate-400">{subtle}</div>
        </div>
    );
}
